.linkContent{
display: flex;
flex-direction: row;
align-items: center;
justify-content: space-around;

padding: 20px 10px;
font-size: x-large;
font-family: "Nunito";


}
Link{
    border: 1px solid red;
    font-family: "Nunito";

}
.linkContent{
    color: #800000;
}
.custom-link{
    text-decoration: none;
    color: #800000;
    background-color: whitesmoke;
    border: 1px solid black;
    padding: 10px 40px;
    border-radius: 20px;
}
.custom-link:hover{
    color: white;
    background-color: #5cdb95;
}